* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  background: url("./src/assets/rule-x.svg") 0 0 no-repeat #32485f;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
}

.main-container {
  font-size: 2.875rem;
}

@media screen and (min-width:40.063em) {
  .main-container {
    font-size: 4rem;
  }
}

h1 {
  font-size: 1rem;
  font-weight: normal;
  text-align: center;
}

p {
  margin-bottom: 3rem;
}

p:last-child {
  margin-bottom: O;
}
